.tile{align-content:space-between;align-items:flex-start;display:flex}.tile .tile-icon,.tile .tile-action{flex:0 0 auto}.tile .tile-content{flex:1 1 auto}.tile .tile-content:not(:first-child){padding-left:.4rem}.tile .tile-content:not(:last-child){padding-right:.4rem}.tile .tile-title,.tile .tile-subtitle{line-height:1.2rem}.tile.tile-centered{align-items:center}.tile.tile-centered .tile-content{overflow:hidden}.tile.tile-centered .tile-title,.tile.tile-centered .tile-subtitle{overflow:hidden;text-overflow:ellipsis;white-space:nowrap;margin-bottom:0}